<template>
    <div id="index-page">
        <v-card id="form" elevation="10">
            <div v-show="step == 1">
                <v-card-text>
                    <vue-dropzone ref="myVueDropzone" id="dropzone" :options="dropzoneOptions"></vue-dropzone>
                </v-card-text>
                <v-card-actions class="actions">
                    <v-btn color="primary" @click="upload" :disabled="!hasFile">Upload</v-btn>
                </v-card-actions>
            </div>
            <template v-if="step == 2">
                <form>
                    <v-card-text>
                        <v-radio-group row v-model="radio1" hide-details>
                            <v-radio label="Option 1" value="1"></v-radio>
                            <v-radio label="Option 2" value="2"></v-radio>
                        </v-radio-group>

                        <v-radio-group row v-model="radio2" hide-details>
                            <v-radio label="Option 1" value="1"></v-radio>
                            <v-radio label="Option 2" value="2"></v-radio>
                            <v-radio label="Option 3" value="3"></v-radio>
                        </v-radio-group>

                        <v-select label="Dropdown 1" hide-details>
                        </v-select>

                        <v-select label="Dropdown 2" hide-details>
                        </v-select>

                        <v-text-field type="email" label="E-Mail (optional)" />
                    </v-card-text>
                    <v-card-actions class="actions">
                        <v-btn color="primary" @click="step++">Submit</v-btn>
                    </v-card-actions>
                </form>
            </template>
        </v-card>
        <v-stepper light>
            <v-stepper-step step="1" :complete="step > 1">
                Upload a File
            </v-stepper-step>
            <v-stepper-step step="2" :complete="step > 2">
                Fill the Form
            </v-stepper-step>
            <v-stepper-step step="3" :complete="step > 3">
                Checkout
            </v-stepper-step>
        </v-stepper>
    </div>
</template>

<script>
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'

export default {
    components: { vueDropzone: vue2Dropzone },
    data: () => ({
        dropzoneOptions: {
            url: 'https://httpbin.org/post',
            thumbnailWidth: 150,
            maxFilesize: 500,
            headers: { "My-Awesome-Header": "header value" },
            autoProcessQueue: false,
            uploadMultiple: false,
            maxFiles: 1
        },
        hasFile: false,
        step: 1,
        radio1: 1,
        radio2: 1
    }),
    methods: {
        upload() {
            const v = this;
            const dz = this.$refs.myVueDropzone;
        
            dz.dropzone.processQueue();

            console.log("dz.dropzone", dz.dropzone);
        }
    },
    mounted() {
        const v = this;
        const dz = this.$refs.myVueDropzone;
        dz.dropzone.on("complete", function(file) {
            console.log("completed", v);
            v.step++;
        });
        dz.dropzone.on("addedfile", function() {
            console.log("added file");
            v.hasFile = true;
        });
        dz.dropzone.on("removedfile", function() {
            console.log("removed file");
            v.hasFile = false;
        });
    }
}
</script>

<style scoped>
    #index-page {
        display: flex;
    }

    #form {
        width: 300px;
        height: 420px;
    }

    #dropzone {
        max-height: 320px;
        height: 320px;
        overflow: hidden;
    }

    .actions {
        justify-content: center;
    }

    .v-stepper {
        box-shadow: none;
        background: none;
        margin-left: 50px;

        font-size: 2em;
    }

    .v-stepper >>> .v-stepper__label {
        margin-left: 20px;
    }
</style>
